.microsoft {
  &__body {
    width: 88%;
    margin: 0 auto;
    
    @include laptop {
      width: 100%;
    }
  }
    
  &__questions {
    margin-bottom: fluid(100, 60);
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: rem(100);
    
    @include laptop {
      column-gap: fluid(60, 40);
    }

    @include mobile {
      grid-template-columns: 1fr;
      row-gap: fluid(60, 40);
    }
  }

  &__article {
    display: grid;
    grid-template-columns: 1fr;
    align-self: start;
    row-gap: fluid(40, 20);
  }

  &__preview-description {
    margin-bottom: 0;
  }

  &__preview-button {
    margin-right: auto;
  }

  &__accordion-list {
    display: grid;
    row-gap: fluid(40, 30);

    @include mobile {
      row-gap: fluid(20, 16);
    }

    &--has-counter {
      --accordionListCounterHeight: #{fluid(52, 44)};
      counter-reset: custom-counter;
    
      .microsoft__accordion-item {
        display: flex;
        column-gap: fluid(20, 16);
        counter-increment: custom-counter;
        &::before {
          @include flex-center;
          content: counter(custom-counter, decimal-leading-zero);
          min-width: #{fluid(54, 46)};
          height: var(--accordionListCounterHeight);
          padding-top: rem(3);
          background-color: var(--background-additional);
          @include fluid-text(20, 18);
          font-weight: 700;
          border-radius: var(--radius-small);
          border: var(--border);
        }
      }
    }
  }
  &__accordion-item {
    padding-block: 1rem;
    &:not(:last-child) {
      position: relative;

      &::after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;

        height: rem(2);
        background-image: linear-gradient(
          90deg,
          rgba(15, 17, 35, 0) 0%,
          var(--elements-accent-pale) 50%,
          rgba(15, 17, 35, 0) 100%
        );
      }
    }
  }
  &__content {
      display: grid;
      grid-template-columns: 1fr 3fr;
      column-gap: rem(100);
      padding-inline: fluid(30, 10);
      padding-block: fluid(30, 20);
      background-color: var(--background-additional);
      border: var(--border);
      border-radius: var(--radius);
      user-select: none;
      transition: var(--transition-with-border);

      @include hover {
      border-color: var(--border-hover);
      background-color: var(--background-basic);
    }
    
      @include laptop {
        grid-template-columns: 1fr 2fr;
        column-gap: fluid(60, 40);
      }

      @include mobile {
        grid-template-columns: 1fr;
        row-gap: fluid(60, 40);
      }
    } 

  &__nav {
      padding-top: rem(4);
      display: flex;
      flex-direction: column;
      position: relative;
      row-gap: fluid(40, 20);
      position: relative;

      @include mobile {
        padding-top: 0;
        flex-direction: row;
        flex-wrap: wrap;
        column-gap: rem(40);
      }

      &-item {
        display: flex;
        align-items: center;
        cursor: pointer;

        &--active {
          pointer-events: none;
          .microsoft__nav-btn {
            color: var(--text);
          }
          .microsoft__nav-dot {
            background-color: var(--elements-accent);
            transform: scale(1.5);
          }
        }

        @include hover {
          .microsoft__nav-btn {
            color: var(--text);
            transform: translateX(1rem) scale(1.2);
            transform-origin: 'left center';
          }

          .microsoft__nav-dot {
            background-color: var(--elements-accent);
            transform: scale(1.5);
          }
        }
      }

      &-dot {
        height: rem(12);
        width: rem(12);
        background-color: var(--border-color);
        border-radius: 50%;
        margin-right: fluid(20, 10);
        will-change: transform;
        transition: background-color var(--transition-duration) ease-in-out, transform 0.5s ease-in-out;
      }
    }

    &__screens-wrapper {
      width: 100%;
      display: flex;
      align-items: center;
      overflow: hidden;
    }

    &__item {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      transition: opacity var(--transition-duration) ease-in-out;
    }

    &__image {
      display: flex;
      align-items: center;
      justify-content: center;
      order: 1;

      img {
        width: rem(580);
        border-radius: fluid(10, 6);
      }
    }

    &__description {
      // grid-column: 2 / span 9;
      margin-bottom: fluid(40, 30);
      padding-bottom: fluid(30, 20);
      position: relative;
      align-self: start;

      @include mobile-s {
        margin-bottom: 0;
      }

      &::after {
        width: 100%;
        height: rem(2);
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        background-image: linear-gradient(
          90deg,
          rgba(15, 17, 35, 0) 0%,
          var(--elements-accent-pale) 50%,
          rgba(15, 17, 35, 0) 100%
        );
      }
    }
}

