@use '../helpers/' as *;

.section {
  padding-top: fluid(140, 100);
  
  &__title {
    margin-bottom: fluid(60, 40);
    font-family: var(--font-family-titles);
    text-transform: uppercase;
    letter-spacing: 1.4px;
    -webkit-text-stroke: rem(1) var(--text-basic);
    color: transparent;
  }

  &--contact {
    padding-bottom: fluid(60, 40);
  }
}
